import { writable } from 'svelte/store';
import { NotificationTypeModel } from '../../models/models';
import CommonFunctions from '../../utils/commonFunctions';
import Helpers from './notifications.helpers';
const { subscribe, update } = writable([]);
// CONSTANTS
const NOTIFICATION_TIMEOUT = 6000;
// ACTIONS
const add = (text, type = NotificationTypeModel.INFO) => {
    const id = CommonFunctions.generateId();
    update(notifications => Helpers.add(notifications, text, type, id));
    setTimeout(() => close(id), NOTIFICATION_TIMEOUT);
};
const close = (id) => update(notifications => Helpers.close(notifications, id));
// EXPORTS
export const NotificationsActions = { addNotification: add, closeNotification: close }; // Not in use
export const NotificationsStore = {
    subscribe,
    add,
    close,
    NOTIFICATION_TIMEOUT,
};
