import { writable } from 'svelte/store';
import { ModesModel } from '../../models/models';
const { subscribe, update } = writable(ModesModel.SETUP);
// ACTIONS
const toggle = () => update(currentMode => currentMode === ModesModel.SETUP ? ModesModel.SELECT : ModesModel.SETUP);
// EXPORTS
export const ModeStore = { subscribe, toggle };
