<script lang="ts">
import { createEventDispatcher } from 'svelte';
import { DirectionModel } from '../models/models';

export let placeholder = '';

const dispatch = createEventDispatcher();
let input = '';

const add = (): void => {
  if (input === '' || input === ' ') {
    return;
  }

  dispatch('add', input);
  input = '';
};
</script>

<div class="Input">
  <input type="text" bind:value={input} {placeholder} />
  <button class="button" on:click={add}>Add</button>
  <button class="button button--delete" on:click={() => dispatch('delete')}>
    Delete group
  </button>
  <button class="button" on:click={() => dispatch('move', DirectionModel.DOWN)}>
    Move DOWN
  </button>
  <button class="button" on:click={() => dispatch('move', DirectionModel.UP)}>
    Move UP
  </button>
</div>

<style lang="scss">
.Input {
  display: flex;
  height: 3rem;
}

.button {
  cursor: pointer;
  margin-left: 1rem;
  padding: 0.4rem;

  &--delete {
    background-color: red;
    color: white;
  }
}
</style>
