const LocalStorage = () => {
    const setItem = (data, key) => {
        try {
            localStorage.setItem(key, JSON.stringify(data));
            return true;
        }
        catch (_a) {
            return false;
        }
    };
    const getItem = (key) => {
        try {
            return JSON.parse(localStorage.getItem(key));
        }
        catch (_a) {
            return null;
        }
    };
    return { setItem, getItem };
};
export default LocalStorage();
