<script lang="ts">
import { fade } from 'svelte/transition';
import { SelectedItemId, Items, Relations, Groups } from '../store';

export let groupId: string;
export let itemId: string;

$: itemData = $Items[itemId];
$: anItemIsSelected = !!$SelectedItemId;
$: currentItemIsSelected = $SelectedItemId === itemId;
$: currentItemIsDecendantOfSelected =
  $Relations[$SelectedItemId]?.children.includes(itemId) || false;
$: currentItemIsParentOfSelected =
  $Relations[$SelectedItemId]?.parents.includes(itemId) || false;

const onLabelClick = () => {
  // TODO: move to helpers and add tests
  if (!anItemIsSelected || currentItemIsSelected) {
    Items.toggleItem(itemId);

    return;
  }

  // TODO: create this as derived store
  const groupHierarchyMap = Object.values($Groups).reduce(
    (hierarchies, group) => ({ ...hierarchies, [group.id]: group.order }),
    {}
  );

  const currentItemIsValidChild =
    groupHierarchyMap[$Items[$SelectedItemId].groupId] + 1 ===
    groupHierarchyMap[groupId];

  if (!currentItemIsValidChild) {
    return;
  }

  Items.toggleChild($SelectedItemId, itemId);
};
</script>

{#if itemData}
  <div class="Item" transition:fade={{ duration: 1000 }}>
    <button
      class="label"
      class:label--selected={currentItemIsSelected}
      class:label--child={currentItemIsDecendantOfSelected}
      class:label--parent={currentItemIsParentOfSelected}
      on:click={onLabelClick}
    >
      {itemData.label}
    </button>

    {#if currentItemIsSelected}
      <button class="delete" on:click={() => Items.deleteItemFromGroup(itemId)}>
        X
      </button>
    {/if}
  </div>
{/if}

<style lang="scss">
.Item {
  display: flex;
  font-size: 1.4rem;
  margin: 0.8rem;
}

