  &--selected,
  &:hover {
    background-color: black;
    border-color: rgba(0, 0, 0, 0.1);
    color: white;
  }

  &--child {
    background-color: #0f0;
  }

  &--parent {
    background-color: #090;
  }
}

.delete {
  background-color: red;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  min-width: 2.4rem;
