import { writable } from 'svelte/store';
import Helpers from './groups.helpers';
import { groups } from '../../data';
const { subscribe, update, set } = writable(groups);
// const { subscribe, update, set } = writable({} as GroupStoreModel);
// ACTIONS
const addGroup = (label) => update(groups => Helpers.addGroup(groups, label));
const deleteGroup = (id) => update(groups => Helpers.deleteGroup(groups, id));
// TODO need to remove all existing relations for the two group items involved
const reorderGroup = (id, direction) => update(groups => Helpers.reorderGroup(groups, id, direction));
// EXPORTS
export const GroupsStore = {
    subscribe,
    addGroup,
    deleteGroup,
    reorderGroup,
    set,
};
