import { get } from 'svelte/store';
import { NotificationTypeModel, } from '../../models/models';
import LocalStorage from '../../utils/localStorage';
import { NotificationsStore } from '../notifications/notifications';
import { GroupsStore } from '../groups/groups';
import { ItemsStore } from '../items/items';
const Storage = () => {
    const STORAGE_KEY = 'manager-setup';
    const saveSetup = (groups = get(GroupsStore), items = get(ItemsStore), key = STORAGE_KEY) => {
        const saveSuccessful = LocalStorage.setItem({ groups, items }, key);
        const notificationText = saveSuccessful
            ? 'Setup saved'
            : 'Could not save setup';
        const notificationType = saveSuccessful
            ? NotificationTypeModel.SUCESS
            : NotificationTypeModel.ERROR;
        NotificationsStore.add(notificationText, notificationType);
    };
    const loadSetup = (key = STORAGE_KEY) => {
        const setupFromStorage = LocalStorage.getItem(key);
        if (!setupFromStorage) {
            NotificationsStore.add('Could not load setup', NotificationTypeModel.ERROR);
            return;
        }
        // TODO: validate if loaded setup is correct
        GroupsStore.set(setupFromStorage.groups);
        ItemsStore.set(setupFromStorage.items);
        NotificationsStore.add('Setup loaded', NotificationTypeModel.SUCESS);
    };
    return { saveSetup, loadSetup };
};
export const StorageStore = Storage();
