import { writable, derived } from 'svelte/store';
import Helpers from './items.helpers';
import { itemMap } from '../../data';
import { GroupsStore } from '../groups/groups';
const { subscribe, update, set } = writable(itemMap);
// const { subscribe, update, set } = writable({} as ItemMapModel);
// DERIVED
//TODO: add logic for more efficiency (this does not need to be recalculated on all changes)
const RelationsDerived = derived({ subscribe }, (items) => Helpers.getRelationsMap(items));
const SelectedItemIdDerived = derived({ subscribe }, (items) => Helpers.getSelectedItemId(items));
// UPDATE
const toggleItem = (id) => update(items => Helpers.toggleItem(items, id));
const addItem = (groupId, label) => update(items => Helpers.addItem(items, groupId, label));
const deleteItemFromGroup = (id) => update(items => Helpers.deleteItem(items, id));
const removeGrouplessItems = (groupIds) => update(items => Helpers.removeGrouplessItems(items, groupIds));
const toggleChild = (parentId, childId) => update(items => Helpers.toggleChild(items, parentId, childId));
// TODO: move to helpers and add test
const removeAllChildren = () => update(items => {
    return Object.entries(items).reduce((childlessItems, [id, item]) => (Object.assign(Object.assign({}, childlessItems), { [id]: Object.assign(Object.assign({}, item), { childIds: [] }) })), {});
});
// DEPENDENCIES
// TODO: possible memory leak since unsubscribe not used
GroupsStore.subscribe(groups => removeGrouplessItems(Object.keys(groups)));
// EXPORTS
const ItemsStore = {
    subscribe,
    toggleItem,
    addItem,
    deleteItemFromGroup,
    removeGrouplessItems,
    toggleChild,
    removeAllChildren,
    set,
};
export { ItemsStore, RelationsDerived, SelectedItemIdDerived };
