import CommonFunctions from '../../utils/commonFunctions';
// TODO: add tests
const Helpers = () => {
    const _createNotification = (text, type, id = CommonFunctions.generateId()) => ({ id, text, type });
    const add = (notifications, text, type, id = CommonFunctions.generateId()) => [_createNotification(text, type, id), ...notifications];
    const close = (notifications, id) => notifications.filter(notification => notification.id !== id);
    return { _createNotification, add, close };
};
export default Helpers();
